
import { Direction, Directionality } from '@angular/cdk/bidi';
import { BooleanInput } from '@angular/cdk/coercion';
import { CdkVirtualScrollViewport } from '@angular/cdk/scrolling';
import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  SimpleChanges,
  TemplateRef,
  TrackByFunction,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { InputBoolean, TDSSafeAny } from 'tds-ui/shared/utility';
import { BehaviorSubject, combineLatest, of, Subject } from 'rxjs';
import { delay, filter, map, takeUntil } from 'rxjs/operators';


import { TDSTableDataService } from '../table-data.service';
import { TDSTableStyleService } from '../table-style.service';
import {
  TDSTableLayout,
  TDSTablePaginationPosition,
  TDSTableQueryParams,
  TDSTableSize
} from '../table.types';
import { TDSTableInnerScrollComponent } from './table-inner-scroll.component';
import { TDSTableVirtualScrollDirective } from './table-virtual-scroll.directive';
import { ButtonSize, TDSConfigKey, TDSConfigService, WithConfig } from 'tds-ui/core/config';
import { PaginationItemRenderContext } from 'tds-ui/pagination';
import { TDSResizeObserver } from 'tds-ui/core/resize-observers';
import { measureScrollbar } from 'tds-ui/core/util';

const TDS_CONFIG_MODULE_NAME: TDSConfigKey = 'table';

@Component({
  selector: 'tds-table',
  exportAs: 'tdsTable',
  providers: [TDSTableStyleService, TDSTableDataService],
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  template: `
    <tds-spin [delay]="loadingDelay" [spinning]="loading" [indicator]="loadingIndicator" class="h-full relative w-full">
      <ng-container *ngIf="paginationPosition === 'both' || paginationPosition === 'top'">
        <ng-template [ngTemplateOutlet]="paginationTemplate"></ng-template>
      </ng-container>
      <div
        #tableMainElement
        class="tds-table flex flex-col flex-auto w-full"
        [class.tds-table-rtl]="dir === 'rtl'"
        [class.tds-table-fixed-header]="data.length && scrollY"
        [class.tds-table-fixed-column]="scrollX"
        [class.tds-table-has-fix-left]="hasFixLeft"
        [class.tds-table-has-fix-right]="hasFixRight"
        [class.tds-table-bordered]="bordered"
        [class.border-neutral-2-200]="bordered || outerBordered"
        [class.border]="bordered || outerBordered"
        [class.tds-table-out-bordered]="outerBordered && !bordered"
        [class.tds-table-middle]="size === 'middle'"
        [class.tds-table-small]="size === 'small'"
      >
        <tds-table-title-footer 
        class="text-neutral-1-900 text-header-1 font-semibold"
        [ngClass]="{'border-b border-neutral-2-200':bordered || outerBordered}"              
        [title]="title" 
        *ngIf="title">        
        </tds-table-title-footer>
        <tds-table-inner-scroll
         class="flex flex-col h-full w-full"
          *ngIf="scrollY || scrollX; else defaultTemplate"
          [data]="data"
          [scrollX]="scrollX"
          [scrollY]="scrollY"
          [contentTemplate]="contentTemplate"
          [listOfColWidth]="listOfAutoColWidth"
          [theadTemplate]="theadTemplate"
          [verticalScrollBarWidth]="verticalScrollBarWidth"
          [virtualTemplate]="virtualScrollDirective ? virtualScrollDirective.templateRef : null"
          [virtualItemSize]="virtualItemSize"
          [virtualMaxBufferPx]="virtualMaxBufferPx"
          [virtualMinBufferPx]="virtualMinBufferPx"
          [tableMainElement]="tableMainElement"
          [virtualForTrackBy]="virtualForTrackBy"
        ></tds-table-inner-scroll>
        <ng-template #defaultTemplate>
          <tds-table-inner-default
            class="w-full"
            [tableLayout]="tableLayout"
            [listOfColWidth]="listOfManualColWidth"
            [theadTemplate]="theadTemplate"
            [contentTemplate]="contentTemplate"
          ></tds-table-inner-default>
        </ng-template>
        <tds-table-title-footer [footer]="footer" *ngIf="footer"></tds-table-title-footer>
      </div>
      <ng-container *ngIf="paginationPosition === 'both' || paginationPosition === 'bottom'">
        <ng-template [ngTemplateOutlet]="paginationTemplate"></ng-template>
      </ng-container>
    </tds-spin>
    <ng-template #paginationTemplate>
    <div class="w-full pt-2.5" *ngIf="isShowPagination">
    <tds-pagination
    [hidden]="!isShowPagination"
    class="tds-table-pagination tds-table-pagination-right "
    [showSizeChanger]="showSizeChanger"
    [pageSizeOptions]="pageSizeOptions"
    [itemRender]="itemRender!"
    [showQuickJumper]="showQuickJumper"
    [hideOnSinglePage]="hideOnSinglePage"
    [showTotal]="showTotal || rangeTemplate"
    [showRefresh]="showRefresh"
    [size]="paginationType"
    [pageSize]="pageSize"
    [total]="total"
    [simple]="simple"
    [pageIndex]="pageIndex"
    (pageSizeChange)="onPageSizeChange($event)"
    (pageIndexChange)="onPageIndexChange($event)"
    (clickRefresh)="onClickRefresh($event)"
  ></tds-pagination>
    </div>
      
    </ng-template>
    <ng-template #contentTemplate>
      <ng-content></ng-content>
    </ng-template>
    <ng-template #rangeTemplate let-range="range" let-total>
      {{ range[0] }}-{{ range[1] }} of {{ total }}
    </ng-template>
  `,
  host: {
    '[class.tds-table-wrapper-rtl]': 'dir === "rtl"',
    class: "w-full h-full "
  }
})
export class TDSTableComponent<T> implements OnInit, OnDestroy, OnChanges, AfterViewInit {
  readonly _tdsModuleName: TDSConfigKey = TDS_CONFIG_MODULE_NAME;

  static ngAcceptInputType_frontPagination: BooleanInput;
  static ngAcceptInputType_templateMode: BooleanInput;
  static ngAcceptInputType_showPagination: BooleanInput;
  static ngAcceptInputType_loading: BooleanInput;
  static ngAcceptInputType_bordered: BooleanInput;
  static ngAcceptInputType_outerBordered: BooleanInput;
  static ngAcceptInputType_showSizeChanger: BooleanInput;
  static ngAcceptInputType_hideOnSinglePage: BooleanInput;
  static ngAcceptInputType_showQuickJumper: BooleanInput;
  static ngAcceptInputType_simple: BooleanInput;

  @Input() tableLayout: TDSTableLayout = 'auto';
  @Input() showTotal: TemplateRef<{ $implicit: number; range: [number, number] }> | null = null;
  @Input() showRefresh: boolean = true;
  @Input() itemRender: TemplateRef<PaginationItemRenderContext> | null = null;
  @Input() title: string | TemplateRef<TDSSafeAny> | null = null;
  @Input() footer: string | TemplateRef<TDSSafeAny> | null = null;
  @Input() noResult: string | TemplateRef<TDSSafeAny> | undefined = undefined;
  @Input() pageSizeOptions = [10, 20, 30, 40, 50];
  @Input() virtualItemSize = 0;
  @Input() virtualMaxBufferPx = 200;
  @Input() virtualMinBufferPx = 100;
  @Input() virtualForTrackBy: TrackByFunction<T> = index => index;
  @Input() loadingDelay = 0;
  @Input() pageIndex = 1;
  @Input() pageSize = 10;
  @Input() total = 0;
  @Input() widthConfig: ReadonlyArray<string | null> = [];
  @Input() listData: readonly T[] = [];
  @Input() paginationPosition: TDSTablePaginationPosition = 'bottom';
  @Input() scroll: { x?: string | null; y?: string | null } = { x: null, y: 'auto' };
  @Input() paginationType: ButtonSize = 'sm';
  @Input() @InputBoolean() frontPagination = true;
  @Input() @InputBoolean() templateMode = false;
  @Input() @InputBoolean() showPagination = true;
  @Input() @InputBoolean() loading = false;
  @Input() @InputBoolean() outerBordered = false;
  @Input() @WithConfig() loadingIndicator: TemplateRef<TDSSafeAny> | null = null;
  @Input() @WithConfig() @InputBoolean() bordered: boolean = false;
  @Input() @WithConfig() size: TDSTableSize = 'default';
  @Input() @WithConfig() @InputBoolean() showSizeChanger: boolean = false;
  @Input() @WithConfig() @InputBoolean() hideOnSinglePage: boolean = false;
  @Input() @WithConfig() @InputBoolean() showQuickJumper: boolean = false;
  @Input() @WithConfig() @InputBoolean() simple: boolean = false;
  @Output() readonly pageSizeChange = new EventEmitter<number>();
  @Output() readonly pageIndexChange = new EventEmitter<number>();
  @Output() readonly queryParams = new EventEmitter<TDSTableQueryParams>();
  @Output() readonly currentPageDataChange = new EventEmitter<readonly T[]>();
  @Output() readonly clickRefresh = new EventEmitter<MouseEvent>();
  /** public data for ngFor tr */
  public data: readonly T[] = [];
  public cdkVirtualScrollViewport?: CdkVirtualScrollViewport;
  scrollX: string | null = null;
  scrollY: string | null = null;
  theadTemplate: TemplateRef<TDSSafeAny> | null = null;
  listOfAutoColWidth: ReadonlyArray<string | null> = [];
  listOfManualColWidth: ReadonlyArray<string | null> = [];
  hasFixLeft = false;
  hasFixRight = false;
  isShowPagination = true;
  private destroy$ = new Subject<void>();
  private loading$ = new BehaviorSubject<boolean>(false);
  private templateMode$ = new BehaviorSubject<boolean>(false);
  dir: Direction = 'ltr';
  @ContentChild(TDSTableVirtualScrollDirective, { static: false })
  virtualScrollDirective!: TDSTableVirtualScrollDirective<T>;
  @ViewChild(TDSTableInnerScrollComponent) tdsTableInnerScrollComponent!: TDSTableInnerScrollComponent<T>;
  verticalScrollBarWidth = 0;
  onPageSizeChange(size: number): void {
    this.tdsTableDataService.updatePageSize(size);
  }

  onPageIndexChange(index: number): void {
    this.tdsTableDataService.updatePageIndex(index);
  }

  constructor(
    private elementRef: ElementRef,
    private tdsResizeObserver: TDSResizeObserver,
    private tdsConfigService: TDSConfigService,
    private cdr: ChangeDetectorRef,
    private tdsTableStyleService: TDSTableStyleService,
    private tdsTableDataService: TDSTableDataService<T>,
    @Optional() private directionality: Directionality
  ) {
    // TODO: move to host after View Engine deprecation
    this.elementRef.nativeElement.classList.add('tds-table-wrapper');
    this.tdsConfigService
      .getConfigChangeEventForComponent(TDS_CONFIG_MODULE_NAME)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.cdr.markForCheck();
      });
  }

  ngOnInit(): void {
    const { pageIndexDistinct$, pageSizeDistinct$, listOfCurrentPageData$, total$, queryParams$ } =
      this.tdsTableDataService;
    const { theadTemplate$, hasFixLeft$, hasFixRight$ } = this.tdsTableStyleService;

    this.dir = this.directionality.value;
    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.cdr.detectChanges();
    });

    queryParams$.pipe(takeUntil(this.destroy$)).subscribe(this.queryParams);
    pageIndexDistinct$.pipe(takeUntil(this.destroy$)).subscribe(pageIndex => {
      if (pageIndex !== this.pageIndex) {
        this.pageIndex = pageIndex;
        this.pageIndexChange.next(pageIndex);
      }
    });
    pageSizeDistinct$.pipe(takeUntil(this.destroy$)).subscribe(pageSize => {
      if (pageSize !== this.pageSize) {
        this.pageSize = pageSize;
        this.pageSizeChange.next(pageSize);
      }
    });
    total$
      .pipe(
        takeUntil(this.destroy$),
        filter(() => this.frontPagination)
      )
      .subscribe(total => {
        if (total !== this.total) {
          this.total = total;
          this.cdr.markForCheck();
        }
      });
    listOfCurrentPageData$.pipe(takeUntil(this.destroy$)).subscribe(data => {
      this.data = data;
      this.currentPageDataChange.next(data);
      this.cdr.markForCheck();
    });

    theadTemplate$.pipe(takeUntil(this.destroy$)).subscribe(theadTemplate => {
      this.theadTemplate = theadTemplate;
      this.cdr.markForCheck();
    });

    hasFixLeft$.pipe(takeUntil(this.destroy$)).subscribe(hasFixLeft => {
      this.hasFixLeft = hasFixLeft;
      this.cdr.markForCheck();
    });

    hasFixRight$.pipe(takeUntil(this.destroy$)).subscribe(hasFixRight => {
      this.hasFixRight = hasFixRight;
      this.cdr.markForCheck();
    });

    combineLatest([total$, this.loading$, this.templateMode$])
      .pipe(
        map(([total, loading, templateMode]) => total === 0 && !loading && !templateMode),
        takeUntil(this.destroy$)
      )
      .subscribe(empty => {
        this.tdsTableStyleService.setShowEmpty(empty);
      });

    this.verticalScrollBarWidth = measureScrollbar('vertical');
    this.tdsTableStyleService.listOfListOfThWidthPx$.pipe(takeUntil(this.destroy$)).subscribe(listOfWidth => {
      this.listOfAutoColWidth = listOfWidth;
      this.cdr.markForCheck();
    });
    this.tdsTableStyleService.manualWidthConfigPx$.pipe(takeUntil(this.destroy$)).subscribe(listOfWidth => {
      this.listOfManualColWidth = listOfWidth;
      this.cdr.markForCheck();
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    const {
      scroll,
      pageIndex,
      pageSize,
      frontPagination,
      listData,
      widthConfig,
      noResult,
      loading,
      templateMode
    } = changes;

    if (pageIndex) {
      this.tdsTableDataService.updatePageIndex(this.pageIndex);
    }
    if (pageSize) {
      this.tdsTableDataService.updatePageSize(this.pageSize);
    }
    if (listData) {
      this.listData = this.listData || [];
      this.tdsTableDataService.updateListOfData(this.listData);
    }
    if (frontPagination) {
      this.tdsTableDataService.updateFrontPagination(this.frontPagination);
    }
    if (scroll) {
      this.setScrollOnChanges();
    }
    if (widthConfig) {
      this.tdsTableStyleService.setTableWidthConfig(this.widthConfig);
    }
    if (loading) {
      this.loading$.next(this.loading);
    }
    if (templateMode) {
      this.templateMode$.next(this.templateMode);
    }
    if (noResult) {
      this.tdsTableStyleService.setNoResult(this.noResult);
    }

    this.updateShowPagination();
  }

  ngAfterViewInit(): void {
    //fix lỗi expand ko lấy dc host width
    of(this.getWidthHost()).pipe(delay(10), takeUntil(this.destroy$)).subscribe(this.tdsTableStyleService.hostWidth$);
    this.tdsResizeObserver
      .observe(this.elementRef)
      .pipe(
        map(([entry]) => {
          const { width } = entry.target.getBoundingClientRect();
          const scrollBarWidth = this.scrollY ? this.verticalScrollBarWidth : 0;
          return Math.floor(width - scrollBarWidth);
        }),
        takeUntil(this.destroy$)
      )
      .subscribe(this.tdsTableStyleService.hostWidth$);
    if (this.tdsTableInnerScrollComponent && this.tdsTableInnerScrollComponent.cdkVirtualScrollViewport) {
      this.cdkVirtualScrollViewport = this.tdsTableInnerScrollComponent.cdkVirtualScrollViewport;
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
  onClickRefresh(e: MouseEvent) {
    this.clickRefresh.emit(e);
  }
  private setScrollOnChanges(): void {
    this.scrollX = (this.scroll && this.scroll.x) || null;
    this.scrollY = (this.scroll && this.scroll.y) || null;
    this.tdsTableStyleService.setScroll(this.scrollX, this.scrollY);
  }

  private updateShowPagination(): void {
    this.isShowPagination =
      this.showPagination && (
        (this.hideOnSinglePage && this.listData.length > this.pageSize) ||
        (this.listData.length > 0 && !this.hideOnSinglePage) ||
        (!this.frontPagination && this.total > this.pageSize)
      );

  }
  private getWidthHost() {
    const { width } = this.elementRef.nativeElement.getBoundingClientRect();
    const scrollBarWidth = this.scrollY ? this.verticalScrollBarWidth : 0;
    return Math.floor(width - scrollBarWidth);
  }
}
