
import { ChangeDetectionStrategy, Component, Input, TemplateRef, ViewEncapsulation } from '@angular/core';
import { TDSSafeAny } from 'tds-ui/shared/utility';

import { TDSTableLayout } from '../table.types';

@Component({
  selector: 'table[tds-table-content]',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  template: `
    <col  [style.width]="width" [style.minWidth]="width" *ngFor="let width of listOfColWidth" />
    <thead class="tds-table-thead text-neutral-1-900 text-body-2 font-semibold" *ngIf="theadTemplate">
      <ng-template [ngTemplateOutlet]="theadTemplate"></ng-template>
    </thead>
    <ng-template [ngTemplateOutlet]="contentTemplate"></ng-template>
    <ng-content></ng-content>
  `,
  host: {
    '[style.table-layout]': 'tableLayout',
    '[class.tds-table-fixed]': 'scrollX',
    '[style.width]': 'scrollX',
    '[style.min-width]': `scrollX ? '100%': null`,
    'class':"w-full"
  }
})
export class TDSTableContentComponent {
  @Input() tableLayout: TDSTableLayout = 'auto';
  @Input() theadTemplate: TemplateRef<TDSSafeAny> | null = null;
  @Input() contentTemplate: TemplateRef<TDSSafeAny> | null = null;
  @Input() listOfColWidth: ReadonlyArray<string | null> = [];
  @Input() scrollX: string | null = null;
}
