
import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  OnDestroy,
  OnInit,
  Renderer2,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { TDSTableStyleService } from '../table-style.service';

@Component({
  selector: 'tr[tds-table-fixed-row], tr[expand]',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  template: `
    <td class="tds-disable-td tds-table-cell border-b-0" #tdElement>
      <div
        class="tds-table-expanded-row-fixed p-0"
        *ngIf="enableAutoMeasure$ | async; else contentTemplate"
        style="position: sticky; left: 0px; overflow: hidden;"
        [style.width.px]="hostWidth$ | async"
      >
        <ng-template [ngTemplateOutlet]="contentTemplate"></ng-template>
      </div>
    </td>
    <ng-template #contentTemplate><ng-content></ng-content></ng-template>
  `
})
export class TDSTableFixedRowComponent implements OnInit, OnDestroy, AfterViewInit {
  @ViewChild('tdElement') tdElement!: ElementRef;
  hostWidth$ = new BehaviorSubject<number | null>(null);
  enableAutoMeasure$ = new BehaviorSubject<boolean>(false);
  private destroy$ = new Subject<void>();
  constructor(private tdsTableStyleService: TDSTableStyleService, private renderer: Renderer2,
    private cdr: ChangeDetectorRef) {}
  ngOnInit(): void {
    if (this.tdsTableStyleService) {
      const { enableAutoMeasure$, hostWidth$ } = this.tdsTableStyleService;
      enableAutoMeasure$.pipe(takeUntil(this.destroy$)).subscribe(this.enableAutoMeasure$);
      hostWidth$.subscribe({
        next:(res)=>{
          this.hostWidth$.next(res);
          this.cdr.markForCheck();
        }
      });
    }
  }
  ngAfterViewInit(): void {
    this.tdsTableStyleService.columnCount$.pipe(takeUntil(this.destroy$)).subscribe(count => {
      this.renderer.setAttribute(this.tdElement.nativeElement, 'colspan', `${count}`);
    });
  }
  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
