
import { Directive, TemplateRef } from '@angular/core';

import { TDSSafeAny } from 'tds-ui/shared/utility';

@Directive({
  selector: '[tds-virtual-scroll]',
  exportAs: 'tdsVirtualScroll'
})
export class TDSTableVirtualScrollDirective<T> {
  constructor(public templateRef: TemplateRef<{ $implicit: T; index: number }>) {}

  static ngTemplateContextGuard<T>(
    _dir: TDSTableVirtualScrollDirective<T>,
    _ctx: TDSSafeAny
  ): _ctx is { $implicit: T; index: number } {
    return true;
  }
}
