
/* eslint-disable @angular-eslint/component-selector */
import { BooleanInput } from '@angular/cdk/coercion';
import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  SimpleChange,
  SimpleChanges,
  ViewEncapsulation
} from '@angular/core';
import { InputBoolean, TDSSafeAny } from 'tds-ui/shared/utility';


@Component({
  selector: 'th[selections],th[checked],th[showCheckbox],th[showRowSelection]',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <tds-table-selection
      [checked]="checked"
      [disabled]="disabled"
      [indeterminate]="indeterminate"
      [listOfSelections]="selections"
      [showCheckbox]="showCheckbox"
      [showRowSelection]="showRowSelection"
      (checkedChange)="onCheckedChange($event)"
    ></tds-table-selection>
    <ng-content></ng-content>
  `,

})
export class TDSThSelectionComponent implements OnChanges {
  static ngAcceptInputType_showCheckbox: BooleanInput;
  static ngAcceptInputType_showRowSelection: BooleanInput;
  @Input() selections: Array<{ text: string; onSelect(...args: TDSSafeAny[]): TDSSafeAny }> = [];
  @Input() checked = false;
  @Input() disabled = false;
  @Input() indeterminate = false;
  @Input() @InputBoolean() showCheckbox = false;
  @Input() @InputBoolean() showRowSelection = false;
  @Output() readonly checkedChange = new EventEmitter<boolean>();

  private isNzShowExpandChanged = false;
  private isNzShowCheckboxChanged = false;

  constructor(private elementRef: ElementRef) {
    // TODO: move to host after View Engine deprecation
    this.elementRef.nativeElement.classList.add('tds-table-selection-column');
  }

  onCheckedChange(checked: boolean): void {
    this.checked = checked;
    this.checkedChange.emit(checked);
  }

  ngOnChanges(changes: SimpleChanges): void {
    const isFirstChange = (value: SimpleChange) => value && value.firstChange && value.currentValue !== undefined;
    const { checked, selections, showExpand, showCheckbox } = changes;
    if (showExpand) {
      this.isNzShowExpandChanged = true;
    }
    if (showCheckbox) {
      this.isNzShowCheckboxChanged = true;
    }
    if (isFirstChange(selections) && !this.isNzShowExpandChanged) {
      this.showRowSelection = true;
    }
    if (isFirstChange(checked) && !this.isNzShowCheckboxChanged) {
      this.showCheckbox = true;
    }
    
  }
}
