
import { AfterContentInit, ContentChildren, Directive,  Input, OnDestroy, Optional, QueryList } from '@angular/core';
import { combineLatest, merge, Observable, ReplaySubject, Subject } from 'rxjs';
import { map, mergeMap, startWith, switchMap, takeUntil } from 'rxjs/operators';

import { TDSCellFixedDirective } from '../cell/cell-fixed.directive';
import { TDSThMeasureDirective } from '../cell/th-measure.directive';
import { TDSTableStyleService } from '../table-style.service';

@Directive({
  selector:
    'tr:not([mat-row]):not([mat-header-row]):not([tds-table-measure-row]):not([expand]):not([tds-table-fixed-row]):not([tds-table-thead])',
  host: {
    '[class.tds-table-row]': 'isInsideTable',
    // '[class.border-b]': 'isInsideTable && !isTrHeader',
    // '[class.border-neutral-2-100]': 'isInsideTable',
     '[class.text-left]': 'isInsideTable',  
    
  }
})
export class TDSTrDirective implements AfterContentInit, OnDestroy {
  @Input() isTrHeader :boolean =false;
  @ContentChildren(TDSThMeasureDirective) listOfNzThDirective!: QueryList<TDSThMeasureDirective>;
  @ContentChildren(TDSCellFixedDirective) listOfCellFixedDirective!: QueryList<TDSCellFixedDirective>;
  private destroy$ = new Subject<void>();
  private listOfFixedColumns$ = new ReplaySubject<TDSCellFixedDirective[]>(1);
  private listOfColumns$ = new ReplaySubject<TDSThMeasureDirective[]>(1);
  listOfFixedColumnsChanges$: Observable<TDSCellFixedDirective[]> = this.listOfFixedColumns$.pipe(
    switchMap(list =>
      merge(...[this.listOfFixedColumns$, ...list.map((c: TDSCellFixedDirective) => c.changes$)]).pipe(
        mergeMap(() => this.listOfFixedColumns$)
      )
    ),
    takeUntil(this.destroy$)
  );
  listOfFixedLeftColumnChanges$ = this.listOfFixedColumnsChanges$.pipe(
    map(list => list.filter(item => item.tdsLeft !== false))
  );
  listOfFixedRightColumnChanges$ = this.listOfFixedColumnsChanges$.pipe(
    map(list => list.filter(item => item.tdsRight !== false))
  );
  listOfColumnsChanges$: Observable<TDSThMeasureDirective[]> = this.listOfColumns$.pipe(
    switchMap(list =>
      merge(...[this.listOfColumns$, ...list.map((c: TDSThMeasureDirective) => c.changes$)]).pipe(
        mergeMap(() => this.listOfColumns$)
      )
    ),
    takeUntil(this.destroy$)
  );
  isInsideTable = false;
 
  constructor(@Optional() private tdsTableStyleService: TDSTableStyleService
  ) {
    this.isInsideTable = !!tdsTableStyleService;
    
  }

  ngAfterContentInit(): void {
    if (this.tdsTableStyleService) {
      this.listOfCellFixedDirective.changes
        .pipe(startWith(this.listOfCellFixedDirective), takeUntil(this.destroy$))
        .subscribe(this.listOfFixedColumns$);
      this.listOfNzThDirective.changes
        .pipe(startWith(this.listOfNzThDirective), takeUntil(this.destroy$))
        .subscribe(this.listOfColumns$);
      /** set last left and first right **/
      this.listOfFixedLeftColumnChanges$.subscribe(listOfFixedLeft => {
        listOfFixedLeft.forEach(cell => cell.setIsLastLeft(cell === listOfFixedLeft[listOfFixedLeft.length - 1]));
      });
      this.listOfFixedRightColumnChanges$.subscribe(listOfFixedRight => {
        listOfFixedRight.forEach(cell => cell.setIsFirstRight(cell === listOfFixedRight[0]));
      });
      /** calculate fixed tdsLeft and tdsRight **/
      combineLatest([this.tdsTableStyleService.listOfListOfThWidth$, this.listOfFixedLeftColumnChanges$])
        .pipe(takeUntil(this.destroy$))
        .subscribe(([listOfAutoWidth, listOfLeftCell]) => {
          listOfLeftCell.forEach((cell, index) => {
            if (cell.isAutoLeft) {
              const currentArray = listOfLeftCell.slice(0, index);
              const count = currentArray.reduce((pre, cur) => pre + (cur.colspan || cur.colSpan || 1), 0);
              const width = listOfAutoWidth.slice(0, count).reduce((pre, cur) => pre + cur, 0);
              cell.setAutoLeftWidth(`${width}px`);
            }
          });
        });
      combineLatest([this.tdsTableStyleService.listOfListOfThWidth$, this.listOfFixedRightColumnChanges$])
        .pipe(takeUntil(this.destroy$))
        .subscribe(([listOfAutoWidth, listOfRightCell]) => {
          listOfRightCell.forEach((_, index) => {
            const cell = listOfRightCell[listOfRightCell.length - index - 1];
            if (cell.isAutoRight) {
              const currentArray = listOfRightCell.slice(listOfRightCell.length - index, listOfRightCell.length);
              const count = currentArray.reduce((pre, cur) => pre + (cur.colspan || cur.colSpan || 1), 0);
              const width = listOfAutoWidth
                .slice(listOfAutoWidth.length - count, listOfAutoWidth.length)
                .reduce((pre, cur) => pre + cur, 0);
              cell.setAutoRightWidth(`${width}px`);
            }
          });
        });
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
