
import { ChangeDetectionStrategy, Component, ElementRef, Input, TemplateRef, ViewEncapsulation } from '@angular/core';
import { TDSSafeAny } from 'tds-ui/shared/utility';

import { TDSTableLayout } from '../table.types';

@Component({
  selector: 'tds-table-inner-default',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  template: `
    <div class="tds-table-content">
      <table
        tds-table-content
        [contentTemplate]="contentTemplate"
        [tableLayout]="tableLayout"
        [listOfColWidth]="listOfColWidth"
        [theadTemplate]="theadTemplate"
      ></table>
    </div>
  `
})
export class TDSTableInnerDefaultComponent {
  @Input() tableLayout: TDSTableLayout = 'auto';
  @Input() listOfColWidth: ReadonlyArray<string | null> = [];
  @Input() theadTemplate: TemplateRef<TDSSafeAny> | null = null;
  @Input() contentTemplate: TemplateRef<TDSSafeAny> | null = null;

  constructor(private elementRef: ElementRef) {
    // TODO: move to host after View Engine deprecation
    this.elementRef.nativeElement.classList.add('tds-table-container');
  }
}
