
/* eslint-disable @angular-eslint/component-selector */
import { BooleanInput } from '@angular/cdk/coercion';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChange,
  SimpleChanges,
  ViewEncapsulation
} from '@angular/core';
import { InputBoolean } from 'tds-ui/shared/utility';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  TDSTableFilterFn,
  TDSTableFilterList,
  TDSTableFilterValue,
  TDSTableSortFn,
  TDSTableSortOrder
} from '../table.types';

@Component({
  selector:
    'th[columnKey], th[sortFn], th[sortOrder], th[filters], th[showSort], th[showFilter], th[customFilter]',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <tds-table-filter
      *ngIf="showFilter || customFilter; else notFilterTemplate"
      [contentTemplate]="notFilterTemplate"
      [extraTemplate]="extraTemplate"
      [customFilter]="customFilter"
      [filterMultiple]="filterMultiple"
      [listOfFilter]="filters"
      (filterChange)="onFilterValueChange($event)"
    ></tds-table-filter>
    <ng-template #notFilterTemplate>
      <ng-template [ngTemplateOutlet]="showSort ? sortTemplate : contentTemplate"></ng-template>
    </ng-template>
    <ng-template #extraTemplate>
      <ng-content select="[tds-th-extra]"></ng-content>
      <ng-content select="tds-filter-trigger"></ng-content>
    </ng-template>
    <ng-template #sortTemplate>
      <tds-table-sorters
        [sortOrder]="_sortOrder"
        [_sortDirections]="_sortDirections"
        [contentTemplate]="contentTemplate"
      ></tds-table-sorters>
    </ng-template>
    <ng-template #contentTemplate>
      <ng-content></ng-content>
    </ng-template>
  `,
  host: {
    '[class.tds-table-column-has-sorters]': 'showSort',
    '[class.tds-table-column-sort]': `_sortOrder === 'descend' || _sortOrder === 'ascend'`,
    '(click)': 'emitNextSortValue()',
    'class':"cursor-pointer hover:bg-neutral-3-100"
  }
})
export class TDSThAddOnComponent<T> implements OnChanges, OnInit, OnDestroy {
  static ngAcceptInputType_showSort: BooleanInput;
  static ngAcceptInputType_showFilter: BooleanInput;
  static ngAcceptInputType_customFilter: BooleanInput;

  manualClickOrder$ = new Subject<TDSThAddOnComponent<T>>();
  calcOperatorChange$ = new Subject();
  filterValue: TDSTableFilterValue = null;
  _sortOrder: TDSTableSortOrder = null;
  _sortDirections: TDSTableSortOrder[] = ['ascend', 'descend', null];
  private sortOrderChange$ = new Subject<TDSTableSortOrder>();
  private destroy$ = new Subject<void>();
  private isNzShowSortChanged = false;
  private isNzShowFilterChanged = false;
  @Input() columnKey?: string;
  @Input() filterMultiple = true;
  @Input() sortOrder: TDSTableSortOrder = null;
  @Input() sortPriority: number | boolean = false;
  @Input() sortDirections: TDSTableSortOrder[] = ['ascend', 'descend', null];
  @Input() filters: TDSTableFilterList = [];
  @Input() sortFn: TDSTableSortFn<T> | boolean | null = null;
  @Input() filterFn: TDSTableFilterFn<T> | boolean | null = null;
  @Input() @InputBoolean() showSort = false;
  @Input() @InputBoolean() showFilter = false;
  @Input() @InputBoolean() customFilter = false;
  @Output() readonly checkedChange = new EventEmitter<boolean>();
  @Output() readonly sortOrderChange = new EventEmitter<string | null>();
  @Output() readonly filterChange = new EventEmitter<TDSTableFilterValue>();

  getNextSortDirection(_sortDirections: TDSTableSortOrder[], current: TDSTableSortOrder): TDSTableSortOrder {
    const index = _sortDirections.indexOf(current);
    if (index === _sortDirections.length - 1) {
      return _sortDirections[0];
    } else {
      return _sortDirections[index + 1];
    }
  }

  emitNextSortValue(): void {
    if (this.showSort) {
      const nextOrder = this.getNextSortDirection(this._sortDirections, this._sortOrder!);
      this.setSortOrder(nextOrder);
      this.manualClickOrder$.next(this);
    }
  }

  setSortOrder(order: TDSTableSortOrder): void {
    this.sortOrderChange$.next(order);
  }

  clearSortOrder(): void {
    if (this._sortOrder !== null) {
      this.setSortOrder(null);
    }
  }

  onFilterValueChange(value: TDSTableFilterValue): void {
    this.filterChange.emit(value);
    this.filterValue = value;
    this.updateCalcOperator();
  }

  updateCalcOperator(): void {
    this.calcOperatorChange$.next();
  }

  constructor(private cdr: ChangeDetectorRef) { }

  ngOnInit(): void {
    this.sortOrderChange$.pipe(takeUntil(this.destroy$)).subscribe(order => {
      if (this._sortOrder !== order) {
        this._sortOrder = order;
        this.sortOrderChange.emit(order);
      }
      this.updateCalcOperator();
      this.cdr.markForCheck();
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    const {
      sortDirections,
      filters,
      sortOrder,
      sortFn,
      filterFn,
      sortPriority,
      filterMultiple,
      showSort,
      showFilter
    } = changes;
    if (sortDirections) {
      if (this.sortDirections && this.sortDirections.length) {
        this._sortDirections = this.sortDirections;
      }
    }
    if (sortOrder) {
      this._sortOrder = this.sortOrder;
      this.setSortOrder(this.sortOrder);
    }
    if (showSort) {
      this.isNzShowSortChanged = true;
    }
    if (showFilter) {
      this.isNzShowFilterChanged = true;
    }
    const isFirstChange = (value: SimpleChange) => value && value.firstChange && value.currentValue !== undefined;
    if ((isFirstChange(sortOrder) || isFirstChange(sortFn)) && !this.isNzShowSortChanged) {
      this.showSort = true;
    }
    if (isFirstChange(filters) && !this.isNzShowFilterChanged) {
      this.showFilter = true;
    }
    if ((filters || filterMultiple) && this.showFilter) {
      const listOfValue = this.filters.filter(item => item.byDefault).map(item => item.value);
      this.filterValue = this.filterMultiple ? listOfValue : listOfValue[0] || null;
    }
    if (sortFn || filterFn || sortPriority || filters) {
      this.updateCalcOperator();
    }
  }
  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
