

/* eslint-disable @angular-eslint/component-selector */
import {
  AfterContentInit,
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  ContentChildren,
  ElementRef,
  EventEmitter,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  QueryList,
  Renderer2,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { EMPTY, merge, Observable, of, Subject } from 'rxjs';
import { delay, map, mergeMap, startWith, switchMap, takeUntil } from 'rxjs/operators';

import { TDSSafeAny } from 'tds-ui/shared/utility';

import { TDSThAddOnComponent } from '../cell/th-addon.component';
import { TDSTableDataService } from '../table-data.service';
import { TDSTableStyleService } from '../table-style.service';
import { TDSTrDirective } from './tr.directive';

@Component({
  selector: 'thead:not(.tds-table-thead)',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  template: `
    <ng-template #contentTemplate>
      <ng-content></ng-content>
    </ng-template>
    <ng-container *ngIf="!isInsideTable">
      <ng-template [ngTemplateOutlet]="contentTemplate"></ng-template>
    </ng-container>
  `
})
export class TDSTheadComponent<T> implements AfterContentInit, OnDestroy, AfterViewInit, OnInit {
  private destroy$ = new Subject<void>();
  isInsideTable = false;
  @ViewChild('contentTemplate', { static: true }) templateRef!: TemplateRef<TDSSafeAny>;
  @ContentChildren(TDSTrDirective, { descendants: true }) listOfNzTrDirective!: QueryList<TDSTrDirective>;
  @ContentChildren(TDSThAddOnComponent, { descendants: true }) listOfNzThAddOnComponent!: QueryList<
    TDSThAddOnComponent<T>
  >;
  @Output() readonly sortOrderChange = new EventEmitter<{ key: TDSSafeAny; value: string | null }>();

  constructor(
    private elementRef: ElementRef,
    private renderer: Renderer2,
    @Optional() private tdsTableStyleService: TDSTableStyleService,
    @Optional() private tdsTableDataService: TDSTableDataService<T>
  ) {
    this.isInsideTable = !!this.tdsTableStyleService;
  }

  ngOnInit(): void {
    if (this.tdsTableStyleService) {
      this.tdsTableStyleService.setTheadTemplate(this.templateRef);
    }
  }

  ngAfterContentInit(): void {
    if (this.tdsTableStyleService) {
      const firstTableRow$ = this.listOfNzTrDirective.changes.pipe(
        startWith(this.listOfNzTrDirective),
        map(item => item && item.first)
      ) as Observable<TDSTrDirective>;
      const listOfColumnsChanges$ = firstTableRow$.pipe(
        switchMap(firstTableRow => (firstTableRow ? firstTableRow.listOfColumnsChanges$ : EMPTY)),
        takeUntil(this.destroy$)
      );
      listOfColumnsChanges$.subscribe(data => this.tdsTableStyleService.setListOfTh(data));
      /** TODO: need reset the measure row when scrollX change **/
      this.tdsTableStyleService.enableAutoMeasure$
        .pipe(switchMap(enable => (enable ? listOfColumnsChanges$ : of([]))))
        .pipe(takeUntil(this.destroy$))
        .subscribe(data => this.tdsTableStyleService.setListOfMeasureColumn(data));
      const listOfFixedLeftColumnChanges$ = firstTableRow$.pipe(
        switchMap(firstTr => (firstTr ? firstTr.listOfFixedLeftColumnChanges$ : EMPTY)),
        takeUntil(this.destroy$)
      );
      const listOfFixedRightColumnChanges$ = firstTableRow$.pipe(
        switchMap(firstTr => (firstTr ? firstTr.listOfFixedRightColumnChanges$ : EMPTY)),
        takeUntil(this.destroy$)
      );
      listOfFixedLeftColumnChanges$.subscribe(listOfFixedLeftColumn => {
        this.tdsTableStyleService.setHasFixLeft(listOfFixedLeftColumn.length !== 0);
      });
      listOfFixedRightColumnChanges$.subscribe(listOfFixedRightColumn => {
        this.tdsTableStyleService.setHasFixRight(listOfFixedRightColumn.length !== 0);
      });
    }
    if (this.tdsTableDataService) {
      const listOfColumn$ = this.listOfNzThAddOnComponent.changes.pipe(
        startWith(this.listOfNzThAddOnComponent)
      ) as Observable<QueryList<TDSThAddOnComponent<T>>>;
      const manualSort$ = listOfColumn$.pipe(
        switchMap(() => merge(...this.listOfNzThAddOnComponent.map(th => th.manualClickOrder$))),
        takeUntil(this.destroy$)
      );
      manualSort$.subscribe((data: TDSThAddOnComponent<T>) => {      
        const emitValue = { key: data.columnKey, value: data._sortOrder };
        this.sortOrderChange.emit(emitValue);
        if (data.sortFn && data.sortPriority === false) {
          this.listOfNzThAddOnComponent.filter(th => th !== data).forEach(th => th.clearSortOrder());
        }
      });
      const listOfCalcOperator$ = listOfColumn$.pipe(
        switchMap(list =>
          merge(...[listOfColumn$, ...list.map((c: TDSThAddOnComponent<T>) => c.calcOperatorChange$)]).pipe(
            mergeMap(() => listOfColumn$)
          )
        ),
        map(list =>
          list
            .filter(item => !!item.sortFn || !!item.filterFn)
            .map(item => {
              const { sortFn, _sortOrder, filterFn, filterValue, sortPriority, columnKey } = item;
              return {
                key: columnKey,
                sortFn: sortFn,
                sortPriority: sortPriority,
                sortOrder: _sortOrder!,
                filterFn: filterFn!,
                filterValue: filterValue
              };
            })
        ),
        // TODO: after checked error here
        delay(0),
        takeUntil(this.destroy$)
      );
      listOfCalcOperator$.subscribe(list => {
        this.tdsTableDataService.listOfCalcOperator$.next(list);
      });
    }
  }

  ngAfterViewInit(): void {
    if (this.tdsTableStyleService) {
      this.renderer.removeChild(this.renderer.parentNode(this.elementRef.nativeElement), this.elementRef.nativeElement);
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
