
import { BooleanInput } from '@angular/cdk/coercion';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  ViewEncapsulation
} from '@angular/core';
import { TDSDropdownMenuComponent } from 'tds-ui/dropdown';
import { InputBoolean } from 'tds-ui/shared/utility';


@Component({
  selector: 'tds-filter-trigger',
  exportAs: `tdsFilterTrigger`,
  changeDetection: ChangeDetectionStrategy.OnPush,
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  template: `
    <span
      tds-dropdown
      class="tds-table-filter-trigger"
      trigger="click"
      placement="bottomRight"
      [backdrop]="backdrop || hasBackdrop"
      [clickHide]="false"
      [tdsDropdownMenu]="dropdownMenu"
      [class.active]="active"
      [class.tds-table-filter-open]="visible"
      [visible]="visible"
      (visibleChange)="onVisibleChange($event)"
      (click)="onFilterClick($event)"
    >
      <ng-content></ng-content>
    </span>
  `
})
export class TDSFilterTriggerComponent implements OnChanges {
  static ngAcceptInputType_backdrop: BooleanInput;
  static ngAcceptInputType_hasBackdrop: BooleanInput;

  @Input() active = false;
  @Input() dropdownMenu!: TDSDropdownMenuComponent;
  @Input() visible = false;

  /**
   * @deprecated Not supported, use `backdrop` instead.
   * @breaking-change 13.0.0
   */
  @Input() @InputBoolean() hasBackdrop = false;
  @Input() @InputBoolean() backdrop = false;

  @Output() readonly visibleChange = new EventEmitter<boolean>();

  onVisibleChange(visible: boolean): void {
    this.visible = visible;
    this.visibleChange.next(visible);
  }

  onFilterClick($event: MouseEvent): void {
    $event.stopPropagation();
  }

  hide(): void {
    this.visible = false;
    this.cdr.markForCheck();
  }

  show(): void {
    this.visible = true;
    this.cdr.markForCheck();
  }

  constructor(private cdr: ChangeDetectorRef) {}

  ngOnChanges(changes: SimpleChanges): void {
    const { hasBackdrop } = changes;
    // if (hasBackdrop) {
    //   console.warn(
    //     '`hasBackdrop` in tds-filter-trigger component will be removed in 13.0.0, please use `backdrop` instead.'
    //   );
    // }
  }
}
