
/* eslint-disable @angular-eslint/component-selector */

import { BooleanInput } from '@angular/cdk/coercion';
import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  SimpleChange,
  SimpleChanges,
  ViewEncapsulation
} from '@angular/core';
import { InputBoolean } from 'tds-ui/shared/utility';



@Component({
  selector:
    'td[checked], td[disabled], td[indeterminate], td[indentSize], td[expand], td[showExpand], td[showCheckbox]',
  changeDetection: ChangeDetectionStrategy.OnPush,
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  template: `
    <ng-container *ngIf="showExpand || indentSize > 0">
      <tds-row-indent [indentSize]="indentSize"></tds-row-indent>
      <button
        tds-row-expand-button      
        [expand]="expand"
        (expandChange)="onExpandChange($event)"
        [spaceMode]="!showExpand"
        class="p-2 text-neutral-1-500 hover:text-primary-1"
      >
        <span class="flex items-center">
         <i [ngClass]="{'tdsi-arrow-right-line':!expand,'tdsi-arrow-down-line':expand}" class="text-base leading-4"></i>
        </span>
      </button>
    </ng-container>
    <tds-checkbox     
      *ngIf="showCheckbox"
      [disabled]="disabled"
      [ngModel]="checked"
      [indeterminate]="indeterminate"
      (ngModelChange)="onCheckedChange($event)"
    ></tds-checkbox>
    <ng-content></ng-content>
  `,
  host: {
    '[class.tds-table-cell-with-append]': `showExpand || indentSize > 0`,
    '[class.tds-table-selection-column]': `showCheckbox`
  }
})
export class TDSTdAddOnComponent implements OnChanges {
  static ngAcceptInputType_showExpand: BooleanInput;
  static ngAcceptInputType_showCheckbox: BooleanInput;
  static ngAcceptInputType_expand: BooleanInput;

  @Input() checked = false;
  @Input() disabled = false;
  @Input() indeterminate = false;
  @Input() indentSize = 0;
  @Input() @InputBoolean() showExpand = false;
  @Input() @InputBoolean() showCheckbox = false;
  @Input() @InputBoolean() expand = false;
  @Output() readonly checkedChange = new EventEmitter<boolean>();
  @Output() readonly expandChange = new EventEmitter<boolean>();
  private isNzShowExpandChanged = false;
  private isNzShowCheckboxChanged = false;

  onCheckedChange(checked: boolean): void {
    this.checked = checked;
    this.checkedChange.emit(checked);
  }

  onExpandChange(expand: boolean): void {
    this.expand = expand;
    this.expandChange.emit(expand);
  }
  ngOnChanges(changes: SimpleChanges): void {
    const isFirstChange = (value: SimpleChange) => value && value.firstChange && value.currentValue !== undefined;
    const { expand, checked, showExpand, showCheckbox } = changes;
    if (showExpand) {
      this.isNzShowExpandChanged = true;
    }
    if (showCheckbox) {
      this.isNzShowCheckboxChanged = true;
    }
    if (isFirstChange(expand) && !this.isNzShowExpandChanged) {
      this.showExpand = true;
    }
    if (isFirstChange(checked) && !this.isNzShowCheckboxChanged) {
      this.showCheckbox = true;
    }
  }
}
